.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.App button {
  margin: 0 .2rem;
  padding: 1rem 2rem;
  border: 2px solid transparent;
  border-radius: .5rem;
  background-color: paleturquoise;
  font-weight: bold; }

.App button.active {
  border: 2px solid darkcyan; }
